<template>
  <div class="recharge-channel">
    <nav-head title="选择支付方式" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="info-wrap">
      <router-link to="moneyLog" tag="div" class="row-item">
        <div class="value">{{UserInfo.diamond}}</div>
        <div class="describe"><span class="icon-balance-365 icon"></span><span class="txt">我的余额</span></div>
      </router-link>
      <router-link to="/redEnvelopesList" tag="div" class="row-item">
        <div class="value">{{UserInfo.coupon}}</div>
        <div class="describe"><span class="icon-redPack icon"></span><span class="txt">红包</span></div>
      </router-link>
    </div>
    <div class="item-wrap">
      <template v-for="item in RechargeChannels">
        <template v-if="item.type === 4">
          <router-link :to="{ name: 'rechargeByAlipayQRCode', params: { item: item }}" tag="div" class="item" v-bind:key="item.id">
            <span class="icon" v-bind:style="{ backgroundImage: 'url(' + item.image + ')'}"></span>
            <span class="margin-left-30">{{item.name}}</span>
            <span class="arrow-right"></span>
          </router-link>
        </template>
        <template v-else-if="item.type === 5">
          <router-link :to="{ name: 'rechargeByUnionPay', params: { item: item }}" tag="div" class="item" v-bind:key="item.id">
            <span class="icon" v-bind:style="{ backgroundImage: 'url(' + item.image + ')'}"></span>
            <span class="margin-left-30">{{item.name}}</span>
            <span class="arrow-right"></span>
          </router-link>
        </template>
        <template v-else>
          <router-link :to="{ path: '/recharge365', query: { id: item.id }}" tag="div" class="item" v-bind:key="item.id">
            <span class="icon" v-bind:style="{ backgroundImage: 'url(' + item.image + ')'}"></span>
            <span class="margin-left-30">{{item.name}}</span>
            <span class="arrow-right"></span>
          </router-link>
        </template>
      </template>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_RECHARGE_CHANNEL } from '../../../vuex/Mine'

export default {
  name: 'rechargeChannel',
  methods: {
    ...mapActions({
      getRechargeChannels: GET_RECHARGE_CHANNEL
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  },
  computed: {
    ...mapState({
      UserInfo: state => state.mine.User,
      RechargeChannels: state => state.mine.RechargeChannels
    })
  },
  created () {
    this.getRechargeChannels()
  }
}
</script>
<style scoped lang="scss">
  .info-wrap {
    position: fixed;
    z-index: 2;
    width: 100%;
    max-width: 640px;
    background-color: $background-light-color;
    text-align: center;
    padding: 17px 0 17px 0;
    div.row-item {
      display: inline-block;
      width: 50%;
      font-size: 16px;
      color: #333;
      border-right: 1px solid $border-color;
      > div.value {
        font-weight: bold;
        color: $font-color;
        font-size: 20px;
      }
      > div.describe {
        position: relative;
        height: 21px;
        margin-top: 6px;
        > span.icon {
          position: absolute;
          margin-top: -3px;
          margin-left: 0;
        }
        > span.txt{
          display: inline-block;
          margin-left: 30px;
          font-size: 12px;
          color: #ccc;
        }
      }
    }
    div.row-item:last-child {
      border: none;
    }
  }

  .item-wrap {
    background-color: $background-light-color;
    margin: 99px 0 60px 0;
    padding: 0 15px;
    div.item {
      height: 80px;
      line-height: 80px;
      text-align: left;
      font-size: 16px;
      color: $font-color;
      position: relative;
      border-bottom: 1px solid $border-color;
      > span.arrow-right {
        position: absolute;
        right: 0;
        top: 25px;
      }
      > span.icon {
        position: absolute;
        margin-top: 25px;
        left: 0;
        width: 30px;
        height: 30px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
      }
    }
    div.item:last-child {
      border-bottom: none;
    }
  }
  .margin-left-30{
    margin-left: 50px;
  }
</style>
